import React from 'react';
import './login-page.css';
import { createMuiTheme, MuiThemeProvider, Typography } from '@material-ui/core';
import { Login } from './login-page-container';
import InputMask from 'react-input-mask';
import { red } from '@material-ui/core/colors';
import Input from '../common/input/input';
import { Button } from '../common/button/button';
import LoginIcon from '../../../assets/icons/login_icon.svg';

const theme = createMuiTheme({
  palette: {
    error: red,
  },
});

type Props = {
  handleSubmit: (e: React.FormEvent<HTMLFormElement>) => void;
  handleChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  value: Login;
  error: boolean;
};

const LoginPage = ({ value, handleSubmit, handleChange, error }: Props) => {
  return (
    <div className="login">
      <form autoComplete="off" className="login__form" onSubmit={handleSubmit}>
        <img className="login__icon" src={LoginIcon} alt="Login Icon" />
        <div style={{ margin: '0 0 80px 0' }}>
          <Typography className="login__title">Введите ваши учетные данные</Typography>
        </div>
        <MuiThemeProvider theme={theme}>
          <InputMask
            mask="(8) 999-999-99-99"
            onChange={handleChange}
            value={value.login}
            disabled={false}
          >
            {() => (
              <Input
                type="tel"
                className="mb-5"
                placeholder="Номер телефона"
                error={error}
                name="login"
              />
            )}
          </InputMask>
        </MuiThemeProvider>
        <Input
          className="mb-5"
          type="password"
          placeholder="Пароль"
          onChange={handleChange}
          name="password"
          value={value.password}
          error={error}
        />
        <Button type="submit" color="primary">
          Вход
        </Button>
      </form>
    </div>
  );
};

export default LoginPage;
