import { httpFetch } from '../http-fetch';
import { tokenStorage } from '../token-storage';
import { Login } from '../../view/components/login-page/login-page-container';
import { baseUrl } from '../base-url';

const login = async (loginValues: Login) => {
  let status = false;
  const replacePhone = () => {
    return {
      login: loginValues.login.trim().replace(/\s/g, '').replace(/[-]/g, '').replace('(8)', ''),
      password: loginValues.password,
    };
  };
  const value = replacePhone();
  await httpFetch
    .post(`${baseUrl}/login`, {
      login: value.login,
      password: value.password,
    })
    .then(data => {
      if (data && data.login === value.login) {
        tokenStorage.setToken(data.token);
        document.cookie = `auth_token=${data.token}`;
        status = true;
      }
    });
  return status;
};

export default login;
